.product-card {
  position: relative;
  background: white;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
  transition: all 0.3s ease;
  cursor: pointer;
  height: 100%;
  display: flex;
  flex-direction: column;

  &:hover {
    transform: translateY(-4px);
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.15);

    .product-actions {
      opacity: 1;
    }
  }

  .discount-badge {
    position: absolute;
    top: 12px;
    left: 12px;
    background: linear-gradient(135deg, #ff6b6b 0%, #ee5a6f 100%);
    color: white;
    padding: 6px 12px;
    border-radius: 20px;
    font-weight: 700;
    font-size: 14px;
    z-index: 10;
    box-shadow: 0 2px 8px rgba(255, 107, 107, 0.4);

    .discount-rate {
      display: block;
    }
  }

  .product-image {
    position: relative;
    width: 100%;
    padding-top: 100%;
    background: #f5f5f5;
    overflow: hidden;

    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: transform 0.3s ease;
    }

    &:hover img {
      transform: scale(1.05);
    }

    .product-actions {
      position: absolute;
      bottom: 12px;
      right: 12px;
      display: flex;
      gap: 8px;
      opacity: 0;
      transition: opacity 0.3s ease;

      .action-btn {
        background: white;
        box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
        transition: all 0.3s ease;

        mat-icon {
          color: #666;
          font-size: 20px;
          width: 20px;
          height: 20px;
        }

        &:hover {
          transform: scale(1.1);
          
          mat-icon {
            color: #667eea;
          }
        }

        &.favorite.active mat-icon {
          color: #ff6b6b;
        }
      }
    }
  }

  .product-info {
    padding: 16px;
    display: flex;
    flex-direction: column;
    gap: 8px;
    flex: 1;

    .seller-info {
      display: flex;
      align-items: center;
      gap: 4px;
      font-size: 12px;
      color: #999;

      .store-icon {
        font-size: 16px;
        width: 16px;
        height: 16px;
      }

      .seller-name {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }

    .product-name {
      margin: 0;
      font-size: 15px;
      font-weight: 600;
      color: #333;
      line-height: 1.4;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      line-clamp: 2;
      -webkit-box-orient: vertical;
      min-height: 42px;
    }

    .product-rating {
      display: flex;
      align-items: center;
      gap: 6px;

      .stars {
        display: flex;
        gap: 2px;

        .star {
          font-size: 16px;
          width: 16px;
          height: 16px;
          color: #ddd;

          &.filled {
            color: #ffc107;
          }
        }
      }

      .rating-text {
        font-size: 12px;
        color: #666;
      }
    }

    .price-section {
      display: flex;
      flex-direction: column;
      gap: 4px;
      margin-top: auto;

      .price-row {
        display: flex;
        align-items: center;
      }

      .original-price {
        font-size: 14px;
        color: #999;
        text-decoration: line-through;
      }

      .final-price {
        font-size: 20px;
        font-weight: 700;
        color: #667eea;
      }
    }

    .shipping-info {
      display: flex;
      align-items: center;
      gap: 4px;
      font-size: 12px;
      color: #666;
      padding-top: 8px;
      border-top: 1px solid #f0f0f0;

      .shipping-icon {
        font-size: 16px;
        width: 16px;
        height: 16px;
        color: #999;
      }

      .shipping-text {
        font-weight: 500;
      }
    }
  }
}

@media (max-width: 768px) {
  .product-card {
    border-radius: 8px;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.08);
    
    .product-actions {
      opacity: 1;
    }

    .product-info {
      padding: 8px;

      .product-name {
        font-size: 14px;
        min-height: 40px;
      }

      .price-section {
        .final-price {
          font-size: 16px;
        }
      }
    }
  }
}
